import Vue from 'vue'
import Router from 'vue-router'
import Index from '../views/Index/index.vue'
import Order from '../views/Order/index.vue'
Vue.use(Router)

let autoRoutes = [
  {
    path: '/index',
    name: 'index-page',
    component: Index
  },  {
    path: '/order/:no/:id',
    name: 'order-page',
    component: Order
  }
]

let customRoutes = [
  {
    path: '*',
    redirect: '/index'
  }
]

export default new Router({
  routes: autoRoutes.concat(customRoutes)
})
